import React from 'react';
import PropTypes from 'prop-types';
import {
  IconAppStore,
  IconBookmark,
  IconCodepen,
  IconExternal,
  IconFolder,
  IconFork,
  IconGitHub,
  IconInstagram,
  IconLinkedin,
  IconLoader,
  IconLogo,
  IconPlayStore,
  IconStar,
  IconTwitter,
  IconJava,
  IconPython,
  IconJavaScript,
  IconReact,
  IconVue,
  IconNodeJS,
  IconDjango,
  IconSpring,
  IconSymfony,
  IconDocker,
  IconMySQL,
  IconMongoDB,
  IconGit,
  IconAWS,
  IconAzure,
  IconTypeScript,
} from '@components/icons';

const Icon = ({ name }) => {
  switch (name) {
    case 'AppStore':
      return <IconAppStore />;
    case 'Bookmark':
      return <IconBookmark />;
    case 'Codepen':
      return <IconCodepen />;
    case 'External':
      return <IconExternal />;
    case 'Folder':
      return <IconFolder />;
    case 'Fork':
      return <IconFork />;
    case 'GitHub':
      return <IconGitHub />;
    case 'Instagram':
      return <IconInstagram />;
    case 'Linkedin':
      return <IconLinkedin />;
    case 'Loader':
      return <IconLoader />;
    case 'Logo':
      return <IconLogo />;
    case 'PlayStore':
      return <IconPlayStore />;
    case 'Star':
      return <IconStar />;
    case 'Twitter':
      return <IconTwitter />;
    case 'Java':
      return <IconJava />;
    case 'Python':
      return <IconPython />;
    case 'JavaScript':
      return <IconJavaScript />;
    case 'React':
      return <IconReact />;
    case 'Vue':
      return <IconVue />;
    case 'NodeJS':
      return <IconNodeJS />;
    case 'Django':
      return <IconDjango />;
    case 'Spring':
      return <IconSpring />;
    case 'Symfony':
      return <IconSymfony />;
    case 'Docker':
      return <IconDocker />;
    case 'MySQL':
      return <IconMySQL />;
    case 'MongoDB':
      return <IconMongoDB />;
    case 'Git':
      return <IconGit />;
    case 'Aws':
      return <IconAWS />;
    case 'Azure':
      return <IconAzure />;
    case 'TypeScript':
      return <IconTypeScript />;
    default:
      return <IconExternal />;
  }
};

Icon.propTypes = {
  name: PropTypes.string.isRequired,
};

export default Icon;
