import React, { useState, useEffect, useRef } from 'react';
import styled, { keyframes } from 'styled-components';

const walk = keyframes`
  0%, 100% { transform: translateY(0) rotate(0deg); }
  25% { transform: translateY(-5px) rotate(-2deg); }
  75% { transform: translateY(-5px) rotate(2deg); }
`;

const float = keyframes`
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
`;

const WizardContainer = styled.div`
  position: fixed;
  left: ${props => props.position}%;
  bottom: 20px;
  z-index: 9999;
  pointer-events: none;
  transition: left 0.3s ease-out;
  opacity: ${props => props.isVisible ? 1 : 0};
  transition: opacity 0.5s ease, left 0.3s ease-out;

  @media (max-width: 768px) {
    display: none; // Hide on mobile for performance
  }
`;

const WizardImage = styled.img`
  width: 80px;
  height: 100px;
  object-fit: contain;
  animation: ${props => props.isWalking ? walk : float} 0.6s ease-in-out infinite;
  filter: drop-shadow(0 5px 10px rgba(0, 0, 0, 0.3));
  transform: ${props => props.direction === 'left' ? 'scaleX(-1)' : 'scaleX(1)'};
`;

const SparkleEffect = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
`;

const Sparkle = styled.div`
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--green);
  border-radius: 50%;
  animation: sparkleAnimation 1s ease-out forwards;
  box-shadow: 0 0 10px var(--green);

  @keyframes sparkleAnimation {
    0% {
      opacity: 1;
      transform: translate(0, 0) scale(1);
    }
    100% {
      opacity: 0;
      transform: translate(${props => props.x}px, ${props => props.y}px) scale(0);
    }
  }
`;

const WalkingWizard = () => {
  const [scrollProgress, setScrollProgress] = useState(0);
  const [isVisible, setIsVisible] = useState(false);
  const [isWalking, setIsWalking] = useState(false);
  const [direction, setDirection] = useState('right');
  const [sparkles, setSparkles] = useState([]);
  const lastScrollY = useRef(0);
  const sparkleId = useRef(0);
  const walkTimeout = useRef(null);
  const pathPrefix = typeof window !== 'undefined' && window.location.pathname.includes('/Portfolio') ? '/Portfolio' : '';

  useEffect(() => {
    const handleScroll = () => {
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight - windowHeight;
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      const progress = (scrollTop / documentHeight) * 100;

      // Determine direction
      if (scrollTop > lastScrollY.current + 5) {
        setDirection('right');
        setIsWalking(true);
      } else if (scrollTop < lastScrollY.current - 5) {
        setDirection('left');
        setIsWalking(true);
      }

      lastScrollY.current = scrollTop;

      // Show wizard after scrolling a bit
      if (scrollTop > 100) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }

      setScrollProgress(Math.min(95, Math.max(5, progress)));

      // Create sparkles while walking
      if (isWalking && Math.random() > 0.7) {
        createSparkle();
      }

      // Stop walking animation after scroll stops
      clearTimeout(walkTimeout.current);
      walkTimeout.current = setTimeout(() => {
        setIsWalking(false);
      }, 300);
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => {
      window.removeEventListener('scroll', handleScroll);
      clearTimeout(walkTimeout.current);
    };
  }, [isWalking]);

  const createSparkle = () => {
    const id = sparkleId.current++;
    const newSparkle = {
      id,
      x: (Math.random() - 0.5) * 40,
      y: (Math.random() - 0.5) * 40,
    };

    setSparkles(prev => [...prev, newSparkle]);

    // Remove sparkle after animation
    setTimeout(() => {
      setSparkles(prev => prev.filter(s => s.id !== id));
    }, 1000);
  };

  return (
    <WizardContainer position={scrollProgress} isVisible={isVisible}>
      <SparkleEffect>
        {sparkles.map(sparkle => (
          <Sparkle key={sparkle.id} x={sparkle.x} y={sparkle.y} />
        ))}
      </SparkleEffect>
      <WizardImage
        src={`${pathPrefix}/rabio.png`}
        alt="Wizard"
        isWalking={isWalking}
        direction={direction}
      />
    </WizardContainer>
  );
};

export default WalkingWizard;
